@import url(https://fonts.googleapis.com/css?family=Raleway:400,700);
@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.login-page>.container{position:absolute;width:100%;height:100%;overflow:hidden}.login-page>.container:hover .top:before,.login-page>.container:hover .top:after,.login-page>.container:hover .bottom:before,.login-page>.container:hover .bottom:after,.login-page>.container:active .top:before,.login-page>.container:active .top:after,.login-page>.container:active .bottom:before,.login-page>.container:active .bottom:after{margin-left:200px;transform-origin:-200px 50%;transition-delay:0s}.login-page>.container:hover .center,.login-page>.container:active .center{opacity:1;transition-delay:0.2s}.login-page>.container>.top:before,.login-page>.container>.top:after,.login-page>.container>.bottom:before,.login-page>.container>.bottom:after{content:"";display:block;position:absolute;width:200vmax;height:200vmax;top:50%;left:50%;margin-top:-100vmax;transform-origin:0 50%;transition:all 0.5s cubic-bezier(0.445, 0.05, 0, 1);z-index:10;opacity:0.65;transition-delay:0.2s}.login-page>.container>.top:before{transform:rotate(45deg);background:#e46569}.login-page>.container>.top:after{transform:rotate(135deg);background:#ecaf81}.login-page>.container>.bottom:before{transform:rotate(-45deg);background:#60b8d4}.login-page>.container>.bottom:after{transform:rotate(-135deg);background:#3745b5}.login-page>.container>.center{position:absolute;width:400px;height:400px;top:50%;left:50%;margin-left:-200px;margin-top:-200px;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:30px;opacity:0;transition:all 0.5s cubic-bezier(0.445, 0.05, 0, 1);transition-delay:0s;color:#333}.login-page>.container>.center>form{position:relative;align-content:center}.login-page>.container>.center>form>input{width:100%;padding:15px;margin:5px;border-radius:1px;border:1px solid #ccc;font-family:inherit}.login-page>.container>.center>form>button{letter-spacing:10px;position:relative;outline:none;width:60%;padding:15px;margin:5px;margin-top:20px;border-radius:1px;border:1px solid #ccc;font-family:inherit;background-color:white;left:50%;transform:translate(-50%, 0);cursor:pointer}.login-page>.container>.center>form>button:hover{background-color:#77a88d;color:white}*,*:before,*:after{box-sizing:border-box}body{min-height:100vh;font-family:"Raleway", sans-serif}

.home-page{display:flex;align-items:center;justify-content:center;min-height:100vh;background:#00071c}.home-page>ul{position:relative;display:flex;transform:rotate(-25deg) skew(25deg);transform-style:preserve-3d}.home-page>ul li{position:relative;list-style:none;width:60px;height:60px;margin:0px 20px}.home-page>ul li:before{content:"";position:absolute;bottom:-10px;left:-5px;width:100%;height:10px;background:#2a2a2a;trnasform-origin:top;transform:skewX(-41deg)}.home-page>ul li:after{content:"";position:absolute;top:5px;left:-9px;width:9px;height:100%;background:#2a2a2a;trnasform-origin:right;transform:skewY(-49deg)}.home-page>ul li span{position:absolute;top:0;left:0;width:100%;height:100%;display:flex !important;background:#2a2a2a;justify-content:center;align-items:center;color:#fff;font-size:30px !important;transition:1.5s ease-out}.home-page .title{font-size:10px !important;font-weight:bolder !important}.home-page>ul li:hover span{z-index:1000;transition:0.3s;color:#fff;box-shadow:-1px 1px 1px rgba(0,0,0,0.5)}.home-page>ul li:hover span:nth-child(5){transform:translate(40px, -40px);opacity:1}.home-page>ul li:hover span:nth-child(4){transform:translate(30px, -30px);opacity:0.8}.home-page>ul li:hover span:nth-child(3){transform:translate(20px, -20px);opacity:0.6}.home-page>ul li:hover span:nth-child(2){transform:translate(10px, -10px);opacity:0.4}.home-page>ul li:hover span:nth-child(1){transform:translate(0px, 100px) scale(1.5);opacity:1}.home-page>ul li:nth-child(1):hover span{background:#2d9d9b !important}.home-page>ul li:nth-child(2):hover span{background:#2c3456 !important}.home-page>ul li:nth-child(3):hover span{background:#ea6e96 !important}.home-page>ul li:nth-child(4):hover span{background:#fceb00 !important}

.forbidden{background-color:#332851}.forbidden>.base{width:100%;height:100vh;position:relative;overflow:hidden;display:flex;align-items:center;justify-content:center;cursor:pointer;flex-direction:column;-webkit-tap-highlight-color:rgba(255,255,255,0)}.forbidden>.base>h1{-webkit-tap-highlight-color:rgba(255,255,255,0);font-family:"Ubuntu", sans-serif;text-transform:uppercase;text-align:center;font-size:30vw;display:block;margin:0;color:#9ae1e2;position:relative;z-index:0;-webkit-animation:colors 0.4s ease-in-out forwards;animation:colors 0.4s ease-in-out forwards;-webkit-animation-delay:1.7s;animation-delay:1.7s}.forbidden>.base>h1:before{content:"U";position:absolute;top:-9%;right:40%;transform:rotate(180deg);font-size:15vw;color:#f6c667;z-index:-1;text-align:center;-webkit-animation:lock 0.2s ease-in-out forwards;animation:lock 0.2s ease-in-out forwards;-webkit-animation-delay:1.5s;animation-delay:1.5s}.forbidden>.base>h2{font-family:"Cabin", sans-serif;color:#9ae1e2;font-size:5vw;margin:0;text-transform:uppercase;text-align:center;-webkit-animation:colors 0.4s ease-in-out forwards;animation:colors 0.4s ease-in-out forwards;-webkit-animation-delay:2s;animation-delay:2s;-webkit-tap-highlight-color:rgba(255,255,255,0)}.forbidden>.base>h5{font-family:"Cabin", sans-serif;color:#9ae1e2;font-size:2vw;margin:0;text-align:center;opacity:0;-webkit-animation:show 2s ease-in-out forwards;animation:show 2s ease-in-out forwards;color:#ca3074;-webkit-animation-delay:3s;animation-delay:3s;-webkit-tap-highlight-color:rgba(255,255,255,0)}@-webkit-keyframes lock{0%{top:-4%}50%{top:-6%}}@keyframes lock{0%{top:-4%}50%{top:-6%}}@-webkit-keyframes colors{50%{transform:scale(1.1)}100%{color:#ca3074}}@keyframes colors{50%{transform:scale(1.1)}100%{color:#ca3074}}@-webkit-keyframes show{100%{opacity:1}}@keyframes show{100%{opacity:1}}

