@import url("https://fonts.googleapis.com/css?family=Raleway:400,700");

.login-page {
  > .container {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    &:hover,
    &:active {
      .top,
      .bottom {
        &:before,
        &:after {
          margin-left: 200px;
          transform-origin: -200px 50%;
          transition-delay: 0s;
        }
      }

      .center {
        opacity: 1;
        transition-delay: 0.2s;
      }
    }
    > .top,
    > .bottom {
      &:before,
      &:after {
        content: "";
        display: block;
        position: absolute;
        width: 200vmax;
        height: 200vmax;
        top: 50%;
        left: 50%;
        margin-top: -100vmax;
        transform-origin: 0 50%;
        transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
        z-index: 10;
        opacity: 0.65;
        transition-delay: 0.2s;
      }
    }

    > .top {
      &:before {
        transform: rotate(45deg);
        background: #e46569;
      }
      &:after {
        transform: rotate(135deg);
        background: #ecaf81;
      }
    }

    > .bottom {
      &:before {
        transform: rotate(-45deg);
        background: #60b8d4;
      }
      &:after {
        transform: rotate(-135deg);
        background: #3745b5;
      }
    }

    > .center {
      position: absolute;
      width: 400px;
      height: 400px;
      top: 50%;
      left: 50%;
      margin-left: -200px;
      margin-top: -200px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 30px;
      opacity: 0;
      transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
      transition-delay: 0s;
      color: #333;
      > form {
        position: relative;
        align-content: center;
        > input {
          width: 100%;
          padding: 15px;
          margin: 5px;
          border-radius: 1px;
          border: 1px solid #ccc;
          font-family: inherit;
        }
        > button {
          letter-spacing: 10px;
          position: relative;
          outline: none;
          width: 60%;
          padding: 15px;
          margin: 5px;
          margin-top: 20px;
          border-radius: 1px;
          border: 1px solid #ccc;
          font-family: inherit;
          background-color: white;
          left: 50%;
          transform: translate(-50%, 0);
          cursor: pointer;
          &:hover {
            background-color: #77a88d;
            color: white;
          }
        }
      }
    }
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}
body {
  min-height: 100vh;
  font-family: "Raleway", sans-serif;
}
